<template>
  <div class='Three-level-linkage'>
    <el-select v-model="value1" placeholder="请选择" @change='ChangeProvince'>
      <el-option v-for="(val,key,index) in options1" :key="val" :label="val" :value="val">
      </el-option>
    </el-select>
    <el-select v-model="value2" placeholder="请选择" @change='changeCity'>
      <el-option v-for="(val,key,index) in options2" :key="val" :label="val" :value="val">
      </el-option>
    </el-select>
    <el-select v-model="value3" placeholder="请选择">
      <el-option v-for="(val,key,index) in options3" :key="val" :label="val" :value="val">
      </el-option>
    </el-select>
  </div>
</template>
<style scoped>
.Three-level-linkage{
  margin-bottom:20px;
}
</style>
<script> 
import ThreeDate from 'china-area-data'
export default {
  data() {
    return {
      options1: ThreeDate['86'],
      options2: {},
      options3: {},
      value1: '',
      value2: '',
      value3: '',
    }
  },
  methods: {
    // 改变省获取市
    ChangeProvince() {
      this.value2 = ''
      this.value3 = ''
      var value;
      for (var key in this.options1) {
        if (this.value1 == this.options1[key]) {
          value = key
        }
      }
      for (var key in ThreeDate) {
        if (value == key) {
          this.options2 = ThreeDate[key]
        }
      }
    },
    // 改变市获取省
    changeCity() {
      this.value3 = ''
      var value;
      for(var key in this.options2){
        if (this.value2 == this.options2[key]) {
          value = key
        }
      }
       for (var key in ThreeDate) {
        if (value == key) {
          this.options3 = ThreeDate[key]
        }
      }
    }
  }
}
</script>


