<template>
    <div class="employess">
        <!--头部-->
        <div class='header'>
            <CommonHeader @getUserMes='headerBody'></CommonHeader>
        </div>
        <!--左边导航-->
        <div class='set'>
            <Set></Set>
        </div>
        <!--内容模块-->
        <div class='content clearfix'>
            <!--第二级导航-->
            <div class='content-left'>
                <button type="button" class="btn btn-default pull-right" @click="addEmployees" style='margin-top:15px;margin-right:10px;'>
                    <i class='fa fa-plus'></i> 新增</button>
                <div class='content-left-t'>员工列表</div>
                <div class='content-left-b'>
                    <ul>
                        <li v-for='(person,index) in personList' class='personLi' :class="{'current':index==0}" @click='getCurrent(index,person.usersId,person.hospitalId)'>
                            <span class='fa fa-user-circle manager-sign' v-if='person.isAdmin==1'></span>
                            {{person.chineseName}}
                            <span style='color:#337ab7;'>{{person.active==0?' (离职)':''}}</span>
                        </li>
                    </ul>
                </div>
            </div>
            <!--资料显示-->

            <div class='content-right means' v-show='means'>
                <!--上-->
                <div class='content-right-t'>
                    <div class='c-r-t-emText'>个人信息</div>
                    <span class='border-cover'></span>
                    <div class='exit-btn'>
                        <button type="button" class="btn btn-primary upload-btn" @click="exitFn">
                            <i class="fa fa-pencil-square-o" aria-hidden="true"></i> 编辑</button>
                    </div>
                </div>
                <!--上-->
                <!--中-->
                <div class='content-right-c' v-if='permission'>
                    <span>基本资料</span>
                    <span class='second-border'></span>
                </div>
                <!--中-->
                <!--下-->
                <div class='content-right-b'>
                    <div class='content-right-b-c'>
                        <div class='right-b-c-left'>
                            <div class='upload'>
                                <img :src="user.imageUrl || defaultImage" alt="">
                            </div>
                        </div>
                        <div class='right-b-c-right'>
                            <div class='row'>
                                <div class='col-md-6'>
                                    姓名：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{user.chineseName}}
                                </div>
                                <div class='col-md-6'>

                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-md-6'>
                                    <span class='s1'>编号：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{user.euNumber }}</span>
                                </div>
                                <div class='col-md-6'>
                                    <span style='margin-left:50px'>性别：</span>&nbsp;&nbsp;&nbsp;&nbsp;{{user.sex==1?'男':'女'}}
                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-md-6'>
                                    <span class='s4'>职务：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{typeFilter(user.type,user.isAdmin)}}</span>
                                </div>
                                <div class='col-md-6'>
                                    <span class='s5'>职称：&nbsp;&nbsp;&nbsp;&nbsp; {{user.title}}</span>
                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-md-12'>
                                    手机号：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; {{user.euMobile}}
                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-md-12'>
                                    身份证号：&nbsp;&nbsp;&nbsp; {{user.idNumber }}
                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-md-6'>
                                    <span>在职医院：&nbsp;{{user.hospitalName}}</span>
                                </div>
                                <div class='col-md-6'>
                                    <span style='margin-left:50px'>状态：</span>
                                    <span class='s3'>&nbsp;&nbsp;&nbsp;&nbsp;{{activeFilter(user.active)}}</span>
                                </div>
                            </div>
                            <div class='row' v-if='permission'>
                                <div class='col-md-12'>
                                    <span>主治病症：&nbsp;{{user.expertGroup?user.expertGroup.join(','):''}}</span>
                                </div>
                            </div>
                            <div>居住地址：&nbsp; {{user.address}}</div>
                            <div class='row'>
                                <div class='col-md-6'>
                                    <span class='s6'>入职日期：&nbsp;{{user.startDate | dataFilter}}</span>
                                </div>
                                <div class='col-md-6'>
                                    <span class='s7' v-if='user.active==0'>离职日期：&nbsp;{{user.endDate | dataFilter}}</span>
                                </div>
                            </div>
                            <div class='row'>
                                <div class='col-md-12'>
                                    擅长：&nbsp; {{user.goodAt}}
                                </div>
                            </div>
                            <div class='synopsis row'>
                                <div class='col-md-11'>
                                    <span>简介：</span>
                                    {{user.introduction}}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--下-->
                <!--附加additional-->
                <div class='content-right-a' v-if='permission'>
                    <div class='content-right-a-text'>
                        <span>服务相关</span>
                        <span class='second-border'></span>
                    </div>
                    <div class='content-right-a-content content-right-a-content2'>
                        <div class='row'>
                            <div class='col-md-6'>
                                <span class='s4' style='margin-right:5px;'>接诊金额：&nbsp;图文问诊</span>
                                <span class='s4'>{{user.price0}}</span>
                                <span style='margin-left:5px'>元/次</span>
                            </div>
                            <div class='col-md-6'>
                                <span style='margin-right:5px;margin-left:50px;'>视频面诊&nbsp;</span>
                                <span>{{user.price1}}</span>
                                <span style='margin-left:5px'>元/次</span>
                            </div>
                        </div>
                        <div class='row range'>
                            <div class='col-md-12'>
                                <div class='range-text pull-left'>接诊范围：&nbsp;</div>
                                <div class='range-content pull-left'>
                                    <div class='address' v-for='item in user.serviceArea' v-show='isAllcountry'>
                                        <span>{{item.province ||''}}</span>
                                        <span>{{item.city || ''}}</span>
                                        <span>{{item.county|| ''}}</span>
                                    </div>
                                    <div class='address' v-show='!isAllcountry'>
                                        <span>全国</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--附加additional-->
            </div>
            <!--资料编辑-->
            <div class='content-right exit' v-show='exit' onLoad="javascript:document.yourFormName.reset()">
                <!--表单-->
                <el-form :model="user" :rules="rules" ref="user" label-width="100px" class="demo-ruleForm">
                    <div class='content-right-t'>
                        <div class='c-r-t-emText'>个人信息</div>
                        <span class='border-cover'></span>
                        <div class='true-exit-btn'>
                            <button type="button" class="btn btn-default remove-btn" @click='removeFn(index,user.usersId,user.hospitalId)'>
                                <i class='fa fa-times'></i> 取消</button>
                            <button class="btn  btn-primary keep-btnt" @click='keepSubmit(user.usersId,user.hospitalId,"user")' type="button">
                                <i class='fa fa-save'></i> 保存</button>
                        </div>
                    </div>
                    <!--中-->
                    <div class='content-right-c' v-if='permission'>
                        <span>基本资料</span>
                        <span class='second-border'></span>
                    </div>
                    <!--中-->
                    <div class='content-right-b'>
                        <span class='border-cover'></span>
                        <div class='content-right-b-c'>
                            <div class='right-b-c-left'>
                                <div class='upload'>
                                    <img :src="user.imageUrl || defaultImage" alt="">
                                    <el-progress :width='100' type="circle" :percentage="percentage" v-show='progressIsShow' :style="progressStyle"></el-progress>
                                </div>
                                <div class='right-b-c-l-btn'>
                                    <button type="button" class="btn btn-primary upload-btn">选择头像</button>
                                    <div class='fileBox'>
                                        <input type="file" class='realBtn' id='realBtn' accept=".png,.jpg,.gif" @change='uploadHeader'>
                                    </div>
                                </div>
                                <div class='text'>(仅支持jpg/gif/png
                                    <br> 格式,大小不超过2M)</div>
                            </div>
                            <div class='right-b-c-right'>
                                <div class='row'>
                                    <div class='col-md-12'>
                                        <el-form-item label="姓名" prop="chineseName">
                                            <el-input style='width:200px;' v-model="user.chineseName"></el-input>（需要和绑定的银行卡开户姓名一致，请务必填写真实姓名）
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-5'>
                                        <el-form-item label="性别" prop='sex' style='display:inline-block;'>
                                            <el-radio-group v-model="user.sex">
                                                <el-radio :label="1">男</el-radio>
                                                <el-radio :label="0">女</el-radio>
                                            </el-radio-group>
                                        </el-form-item>
                                    </div>
                                    <div class='col-md-7'>
                                        <el-form-item label="编号" v-show='numberShow'>
                                            <el-input :disabled='true' style='width:200px;' v-model="user.euNumber"></el-input>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-5' v-if='permission'>
                                        <el-form-item label="职务" prop='type'>
                                            <el-select v-model="user.type" placeholder="请选择职务" style='width:200px;'>
                                                <el-option label="专家" value="2"></el-option>
                                                <el-option label="管理员" value="4"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </div>
                                    <div class='col-md-5' v-if='!permission'>
                                        <el-form-item label="职务" prop='type'>
                                            <el-select v-model="user.type" placeholder="请选择职务" style='width:200px;'>
                                                <el-option label="医生" value="1"></el-option>
                                                <el-option label="管理员" value="3"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </div>
                                    <div class='col-md-7'>

                                        <el-form-item label="职称" prop='title'>
                                            <el-select v-model="user.title" placeholder="请选择职称" style='width:200px;'>
                                                <el-option label="主任医师" value="主任医师"></el-option>
                                                <el-option label="副主任医师" value="副主任医师"></el-option>
                                                <el-option label="主治医师" value="主治医师"></el-option>
                                                <el-option label="住院医师" value="住院医师"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-12'>
                                        <el-form-item label="手机号" prop="euMobile">
                                            <el-input style='width:200px;' :disabled='mobileIsInput' v-model="user.euMobile" @blur='verifyPhone'></el-input>（用户的登录ID，请填写正确的手机号，密码默认手机号后6位）
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-12'>
                                        <el-form-item label="身份证号" prop="idNumber">
                                            <el-input style='width:200px;' @blur='verifyId' v-model="user.idNumber"></el-input>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-5'>
                                        <el-form-item label="在职医院">
                                            <el-input style='width:200px;' disabled v-model="user.hospitalName"></el-input>
                                        </el-form-item>
                                    </div>
                                    <div class='col-md-7'>
                                        <el-form-item label="状态" prop='active'>
                                            <el-select @visible-change='remindUser(user.usersId)' v-model="user.active" placeholder="请选择状态" style='width:200px;'>
                                                <el-option label="正常使用" value="1"></el-option>
                                                <el-option label="停用" value="0"></el-option>
                                                <el-option label="冻结" value="2"></el-option>
                                            </el-select>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row' v-if='permission'>
                                    <div class='col-md-12'>
                                        <el-form-item label="主治病症" prop="expertGroup">
                                            <el-select v-model="user.expertGroup" multiple :multiple-limit='3' placeholder="请选择" style='width:400px;'>
                                                <el-option v-for="item in diseaseList" :key="item.name" :label="item.name" :value="item.name">
                                                </el-option>
                                            </el-select>
                                            <span>（最多可选择3个）</span>
                                        </el-form-item>

                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-12'>
                                        <el-form-item label="居住地址" prop='address'>
                                            <el-input style='width:500px;' v-model="user.address" placeholder="最多不超过30个字"></el-input>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-5'>
                                        <el-form-item label="入职日期">
                                            <el-input style='width:200px;' disabled v-model="user.startDate"></el-input>
                                        </el-form-item>
                                    </div>
                                    <div class='col-md-7' v-if='user.active==0'>
                                        <el-form-item label="离职日期">
                                            <el-input style='width:200px;' disabled v-model="user.endDate"></el-input>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-11'>
                                        <el-form-item label="擅长" prop="goodAt">
                                             <el-input type="textarea" v-model="user.goodAt" placeholder='最多不超过50个字'></el-input>
                                        </el-form-item>
                                    </div>
                                </div>
                                <div class='row'>
                                    <div class='col-md-11'>
                                        <el-form-item label="简介" prop='introduction'>
                                            <el-input type="textarea" v-model="user.introduction" placeholder='最多不超过200个字'></el-input>
                                        </el-form-item>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                    <!--附加additional-->
                    <div class='content-right-a' v-if='permission'>
                        <div class='content-right-a-text'>
                            <span>服务相关</span>
                            <span class='second-border second-border2'></span>
                        </div>
                        <div class='content-right-a-content'>
                            <div class='row'>
                                <div class='image-text' style='padding-left:100px;'>
                                    <label class="el-form-item__label" style="width: 100px;" for="">接诊金额</label>
                                    <el-form-item label="图文问诊" prop='price0'>
                                        <el-input style='width:100px;margin-right:5px' @blur='judgePrice0' placeholder="请输入金额" v-model='user.price0'></el-input>元/次(限额0~{{imgMoney}}元/次)
                                    </el-form-item>
                                </div>
                            </div>
                            <div class='row video-diagnosis'>
                                <div class='interview'>
                                    <el-form-item label="视频面诊" prop='price1'>
                                        <el-input style='width:100px;margin-right:5px' placeholder="请输入金额" @blur='judgePrice1' v-model='user.price1'></el-input>元/次(限额0~{{videoMoney}}元/次)
                                    </el-form-item>
                                </div>
                            </div>
                            <div class='row range'>
                                <div class='col-md-12 range-input'>
                                    <label class="el-form-item__label" style="width: 100px;" for="">接诊范围</label>
                                    <div class='Three-level-linkage' v-for='(item,index) in ThreeLevelList'>
                                        <el-select v-model="item.value1" placeholder="请选择" @visible-change='ChangeProvince(index)' style='width:160px;'>
                                            <el-option v-for="(val,key) in item.options1" :key="val" :label="val" :value="val">
                                            </el-option>
                                        </el-select>
                                        <el-select v-model="item.value2" placeholder="请选择" @visible-change='changeCity(index)' style='width:200px;'>
                                            <el-option v-for="(val,key) in item.options2" :key="val" :label="val" :value="val">
                                            </el-option>
                                        </el-select>
                                        <el-select v-model="item.value3" placeholder="请选择" style='width:120px;'>
                                            <el-option v-for="(val,key) in item.options3" :key="val" :label="val" :value="val">
                                            </el-option>
                                        </el-select>
                                        <span class='el-icon-plus' @click='addTreeLevel'></span>
                                        <span class='el-icon-delete' @click='removeTreeLevel(index)' v-if='removeBtnShow[index]'></span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!--附加additional-->
                </el-form>
                <!--表单-->

            </div>
        </div>
    </div>
</template>
<script>
import CommonHeader from '../public/header.vue';
import Set from '../public/set.vue';
import $ from 'jquery'
// 日期选择插件
import '../../../static/css/daterangepicker.css'
import '../../../static/js/daterangepicker.js'
//上传图片的封装
import Public from '../../untils/Public.js'
import moment from 'moment'
// 三级联动
import ThreeLevel from '../public/threeLevelLinkage.vue'
import ThreeDate from 'china-area-data'


export default {
    name: 'Employees',
    mounted() {
        // 员工列表高度
        this.getClientHeight()
        // 员工列表
        this.getEmployees()
        // 医院列表
        this.searchHospital()
        // 类型
        this.searchType()
        // 字典查金额
        this.searchMoney()


    },
    data: function() {
        return {
            personList: [],
            means: true,
            exit: false,
            Authorization: '',
            employeesDetail: {},
            userBody: {},//請求体
            user: {
                active: '',
                address: '',
                birthday: '',
                chineseName: '',
                createdBy: '',
                createdWhen: '',
                deleteFlag: '',
                easemobUser: '',
                endDate: '',
                euMobile: '',
                euNumber: '',
                expertGroup: [],
                goodAt: '',
                hospitalId: '',
                hospitalName: '',
                id: '',
                idNumber: '',
                imageUrl: '',
                introduction: '',
                isAdmin: '',
                roleId: '',
                sex: '',
                startDate: '',
                title: '',
                type: '',
                typeName: '',
                updateUser: '',
                updateWhen: '',
                updatedBy: '',
                updatedWhen: '',
                usersId: '',
                price0: '',
                price1: ''
            },
            numberShow: true,//编号的显示隐藏
            editable: false,
            index: 0,
            Authorization: '',
            imgMoney: 100,
            videoMoney: 300,
            // 定义表单规则
            rules: {
                chineseName: [
                    { required: true, message: '请填写姓名', trigger: 'blur' },
                    { validator: (rule, value, callback) => { if (/^([\u4e00-\u9fa5]{1,20}|[a-zA-Z\.\s]{1,20})$/.test(value) == false) { callback(new Error("请填写正确姓名")); } else { callback(); } }, trigger: 'blur' }
                ],
                euNumber: { required: true, message: '请填写编号', trigger: 'blur' },
                type: [
                    { required: true, message: '请填写职务', trigger: 'change' }
                ],
                active: [
                    { required: true, message: '请填写状态', trigger: 'change' }
                ],
                expertGroup: [
                    { type: 'array', required: true, message: '请填写主治病症', trigger: 'change' }
                ],
                title: [
                    { required: true, message: '请填写职称', trigger: 'change' }
                ],
                euMobile: [
                    { required: true, message: '请填写手机', trigger: 'blur' },
                    { validator: (rule, value, callback) => { if (/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/.test(value) == false) { callback(new Error("请填写正确手机")); } else { callback(); } }, trigger: 'blur' }
                ],
                idNumber: [
                    // { required: true, message: '请填写身份证', trigger: 'blur' },
                    { validator: (rule, value, callback) => { if (/(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(value) == false && value != '') { callback(new Error("请填写正确身份证")); } else { callback(); } }, trigger: 'blur' }
                ],
                address: [
                    { min: 0, max: 30, message: '长度最多是30个字符', trigger: 'blur' }
                ],
                // startDate:{ required: true, message: '请选择时间',tigger:'blur'},
                // endDate:{ required: true, message: '请选择时间',tigger:'blur'} ,
                goodAt: [
                    { required: true, message: '请填写擅长项目', trigger: 'blur' },
                    { min: 0, max: 50, message: '长度最多是50个字符', trigger: 'blur' }
                ],
                introduction: [
                    { min: 0, max: 200, message: '长度最多是200个字符', trigger: 'blur' }
                ],
                sex: { required: true, message: '请选择性别' },
                hospitalId: [{ required: true, message: '请填写医院', trigger: 'blur' }],
                price0: [
                    { validator: (rule, value, callback) => { if (isNaN(value)) { callback(new Error("请输入数字")); } else { callback(); } }, trigger: 'blur' },
                    { required: true, message: '请填写价格', trigger: 'blur' }
                ],
                price1: [
                    { validator: (rule, value, callback) => { if (isNaN(value)) { callback(new Error("请输入数字")); } else { callback(); } }, trigger: 'blur' },
                    { required: true, message: '请填写价格', trigger: 'blur' }
                ],
            },
            identity: false,//用来判断是否要验证身份证
            quitInput: true,
            options1: ThreeDate['86'],
            options2: {},
            options3: {},
            value1: '',
            value2: '',
            value3: '',
            ThreeLevelList: [{ options1: ThreeDate['86'], options2: {}, options3: {}, value1: '', value2: '', value3: '' }],
            removeBtnShow: [false],
            permission: false,
            hospitalList: [],
            diseaseList: [
                // { value: "高血压", label: '高血压' },
                // { value: "心电生理", label: '心电生理' },
                // { value: "动脉血管", label: '动脉血管' },
                // { value: "心力衰竭", label: '心力衰竭' },
                // { value: "冠心病", label: '冠心病' },
                // { value: "心肌梗死", label: '心肌梗死' },
                // { value: "粥样硬化", label: '粥样硬化' },
            ],
            roleId: '',
            tname: '',//医院名
            tid: '',//医院id
            defaultImage: '../../../static/img/defaultImage.png',
            treeLevelCount: 1,
            isAllcountry: false,
