        // 改变省获取市
        ChangeProvince(index) {
            this.ThreeLevelList[index].value2 = ''
            this.ThreeLevelList[index].value3 = ''
            var value;
            for (var key in this.ThreeLevelList[index].options1) {
                if (this.ThreeLevelList[index].value1 == this.ThreeLevelList[index].options1[key]) {
                    value = key
                }
            }
            for (var key in ThreeDate) {
                if (value == key) {
                    this.ThreeLevelList[index].options2 = ThreeDate[key]
                }
            }
        },
        // 改变市获取县
        changeCity(index) {
            this.ThreeLevelList[index].value3 = ''
            var value;
            for (var key in this.ThreeLevelList[index].options2) {
                if (this.ThreeLevelList[index].value2 == this.ThreeLevelList[index].options2[key]) {
                    value = key
                }
            }
            for (var key in ThreeDate) {
                if (value == key) {
                    this.ThreeLevelList[index].options3 = ThreeDate[key]
                }
            }
        },
        // 改变省获取市
        ChangeProvince1(index) {
            var value;
            for (var key in this.ThreeLevelList[index].options1) {
                if (this.ThreeLevelList[index].value1 == this.ThreeLevelList[index].options1[key]) {
                    value = key
                }
            }
            for (var key in ThreeDate) {
                if (value == key) {
                    this.ThreeLevelList[index].options2 = ThreeDate[key]
                }
            }
        },
