        // 添加三级联动
        addTreeLevel() {

            if (this.treeLevelCount >= 3) {
                return
            }
            this.treeLevelCount++
            this.ThreeLevelList.push({ options1: ThreeDate['86'], options2: {}, options3: {}, value1: '', value2: '', value3: '' })
            this.removeBtnShow.push(true);
        },
        // 删除三级联动
        removeTreeLevel(index) {
            this.treeLevelCount--
            this.ThreeLevelList.splice(index, 1)
        },
        // 对比roleId
        headerBody(body) {
            this.roleId = body.roleId
            this.tname = body.tname;
            this.tid = body.tid
            if (body.roleId == '1312') {//医生管理员
                this.permission = false
            }
            if (body.roleId == '1313') {//专家管理员
                this.permission = true
            }
