        // 查询所有医院
        searchHospital() {
            var url = '/api/organization/hospital/list';
            this.Authorization = localStorage.getItem('access_token');
            this.$http.get(url, {
                headers: {
                    Authorization: 'bearer ' + this.Authorization
                },
                params: {
                    pageNumber: 1,
                    pageSize: 10000
                }
            }).then(res => {
                this.hospitalList = res.body.list
            })
        },
        // 校验接诊金额
        judgePrice0() {
            if (this.user.price0 != '') {
                if (this.user.price0 <= 0.01) {
                    this.user.price0 = '0.01';
                }
                else if (this.user.price0 > parseInt(this.imgMoney)) {
                    this.user.price0 = this.imgMoney.toString()
                }
            }
        },
        judgePrice1() {
            if (this.user.price1 != '') {
                if (this.user.price1 <= 0.01) {
                    this.user.price1 = '0.01';
                }
                else if (this.user.price1 > parseInt(this.videoMoney)) {
                    this.user.price1 = this.videoMoney.toString()
                }
            }
        },
        searchType() {
            this.Authorization = localStorage.getItem('access_token');
            var url = '/api/patient/patients/type';
            this.$http.get(url, {
                headers: {
                    Authorization: 'bearer ' + this.Authorization
                }
            }).then(function(res) {
                this.diseaseList = res.body;
            })
        },
        // 提醒用户
        remindUser(id) {
            this.Authorization = localStorage.getItem('access_token');
            var url = '/api/assist-order/assistOrders/exist'
            if (this.user.active == 0) {
                if (this.permission) {
                    this.$http.get(url, {
                        headers: {
                            Authorization: 'bearer ' + this.Authorization
                        },
                        params: {
                            userId: id
                        }
                    }).then(res => {
                        if (res.body) {
                            this.$message({
                                message: '该专家存在协诊中或者已预约的订单，请慎重选择状态',
                                type: 'warning'

                            })
                        }
                    })
                } else {
                    this.$http.get(url, {
                        headers: {
                            Authorization: 'bearer ' + this.Authorization
                        },
                        params: {
                            userId: id
                        }
                    }).then(res => {
                        if (res.body) {
                            this.$message({
                                message: '该医生存在协诊中或者已预约的订单，请慎重选择状态',
                                type: 'warning'
                            })
                        }
                    })
                }
            }
        },
        // 字典查金额
        searchMoney() {
            this.Authorization = localStorage.getItem('access_token');
            var url = '/api/basic/dictionary/parent'
            this.$http.get(url, {
                params: {
                    parentId: 106
                }
            }).then(res => {
                this.imgMoney = res.body[0].value
                this.videoMoney = res.body[1].value
            }, err => {
            })

        }
    },
    components: {
        CommonHeader: CommonHeader, Set: Set, ThreeLevel: ThreeLevel
    },
}
</script>
